.container {
  display: flex;
  flex-direction: column;
}

.main {
  display: flex;
  flex-direction: column;
}

.main h1 {
  display: flex;
  flex-direction: column;
  font-size: 2em;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  text-align: center;
  width: 80%;
  margin: 10px auto;
  padding: 0.3em;
  text-transform: uppercase;
}

.content {
  width: 80%;
  margin: 10px auto;
}

.content p {
  padding: 1em;
  text-align: center;
}
